<template>
  <v-card class=" lime lighten-4" style="position: relative; z-index: 100; padding-bottom: 20px; ">
    <v-layout row>
      <v-flex xs10 offset-xs1>
      <!--   <v-card class="lime lighten-4"> -->
          <v-card-title primary-title data-aos="slide-up">
            <h2 class=" pa-1" style="color: #f01663; font-weight: 700; font-size: 30px;">{{title}}</h2>
          </v-card-title>
          <v-card-text data-aos="slide-up" style="margin-top: -20px;">
            <div
              class="pa-1 subheading" style="font-size: 20px !important;"
            >{{text}}</div>
          </v-card-text>
          <v-card-media>
            <v-card flat tile class="d-flex"  >
          
              <v-img data-aos="zoom-in" v-for= "img in images" :key= "img"
                :src="img"
                aspect-ratio="1"
                class="grey lighten-2 ma-2"
                max-width="300"
              >
                <v-layout slot="placeholder" fill-height align-center justify-center ma-0></v-layout>
              </v-img>
        
            </v-card>
          </v-card-media>
          <v-card-actions data-aos="slide-up">
            <v-spacer></v-spacer>
            <v-btn flat style="color: #f01663; font-weight: 700; font-size: 20px;" @click="setTitle" :to="'gallery/'+id">Viac <v-icon>arrow_forward_ios</v-icon></v-btn>
          </v-card-actions>
      <!--   </v-card> -->
      </v-flex>
    </v-layout>
  </v-card>
</template>

<script>
export default {
  props: ["id","title", "text", "images"],
  methods: {
    setTitle()
    {
      this.$store.commit('setTitle',{title: this.title})
      this.$store.commit('setID',{id: this.id})
    }
  }
};
</script>

