<template>
  <div class="container" style="margin-top: 100px;">
    <div class="large-12 medium-12 small-12 cell">
      <h1>Vue JS Axios - Image Upload using PHP API - ItSolutionStuff.com</h1>

      <label>
        File
        <input type="file" id="file" ref="file" v-on:change="onChangeFileUpload()">
      </label>
      
      <button v-on:click="submitForm()">Upload</button>
    </div>
    <hr>
<div id="my-strictly-unique-vue-upload-multiple-image" style="display: flex; justify-content: center;">
<vue-upload-multiple-image
      @upload-success="uploadImageSuccess"
      @before-remove="beforeRemove"
      @edit-image="editImage"
      @data-change="dataChange"
      :data-images="images"
      dragText="dragText"
      browseText="browseText"
      primaryText = "primaryText"
      
    ></vue-upload-multiple-image>
</div>
    <upload-image url="up.php" name="file" :max_files="5"></upload-image>

    <file-upload
          class="btn btn-primary"
          post-action="up.php"
          extensions="gif,jpg,jpeg,png,webp"
          accept="image/png,image/gif,image/jpeg,image/webp"
          :multiple="true"
          :size="1024 * 1024 * 10"
          v-model="files"
          @input-filter="inputFilter"
          @input-file="inputFile"
          ref="upload">
          <i class="fa fa-plus"></i>
          Select files
        </file-upload>
        <button type="button" class="btn btn-success" v-if="!$refs.upload || !$refs.upload.active" @click.prevent="$refs.upload.active = true">
          <i class="fa fa-arrow-up" aria-hidden="true"></i>
          Start Upload
        </button>
        <button type="button" class="btn btn-danger"  v-else @click.prevent="$refs.upload.active = false">
          <i class="fa fa-stop" aria-hidden="true"></i>
          Stop Upload
</button>


  </div>
</template>

<script>
import VueUploadMultipleImage from "vue-upload-multiple-image";
import UploadImage from 'vue-upload-image';
import FileUpload from 'vue-upload-component'

export default {
  components: {
    VueUploadMultipleImage,
    UploadImage,
    FileUpload
  },
  data() {
    return {
      selectedFile: null,
      images: [],
      file: null,
      files:[]
    };
  },
  methods: {
          inputFilter(newFile, oldFile, prevent) {
      if (newFile && !oldFile) {
        // Before adding a file
        // 添加文件前
        // Filter system files or hide files
        // 过滤系统文件 和隐藏文件
        if (/(\/|^)(Thumbs\.db|desktop\.ini|\..+)$/.test(newFile.name)) {
          return prevent()
        }
        // Filter php html js file
        // 过滤 php html js 文件
        if (/\.(php5?|html?|jsx?)$/i.test(newFile.name)) {
          return prevent()
        }
      }
    },

     inputFile(newFile, oldFile) {
      if (newFile && !oldFile) {
        // add
        console.log('add', newFile)
      }
      if (newFile && oldFile) {
        // update
        console.log('update', newFile)
      }
      if (!newFile && oldFile) {
        // remove
        console.log('remove', oldFile)
      }
    },


    uploadImageSuccess(formData, index, fileList) {
      console.log("data", formData, index, fileList);
      window.f = formData
      // Upload image api
      this.axios.post("up.php", { data: formData }).then(response => {
        console.log(response);
      });
    },
    beforeRemove(index, done, fileList) {
      console.log("index", index, fileList);
      var r = confirm("remove image");
      if (r == true) {
        done();
      } else {
        console.log("else")
      }
    },
    editImage(formData, index, fileList) {
      console.log("edit data", formData, index, fileList);
    },
    dataChange(data) {
      console.log(data);
    },

 submitForm(){

            let formData = new FormData();

            formData.append('file', this.file);

            console.log(formData)

            window.x = formData

            this.axios.post('up.php',

                formData,

                {

                headers: {

                    'Content-Type': 'multipart/form-data'

                }

              }

            ).then(function(data){

              console.log(data.data);

            })

            .catch(function(){

              console.log('FAILURE!!');

            });

      },

  

      onChangeFileUpload(){

        this.file = this.$refs.file.files[0];

      },


  },

       



  onFileChanged(event) {
    this.selectedFile = event.target.files[0];
  },
  onUpload() {
    console.log(this.selectedFile);
    //this.axios.post('/', this.selectedFile)
    const formData = new FormData();
    formData.append("myFile", this.selectedFile, this.selectedFile.name);
    this.axios.post("my-domain.com/file-upload", formData, {
      onUploadProgress: progressEvent => {
        console.log(progressEvent.loaded / progressEvent.total);
      }
    });
  }
};
</script>

<style>

#my-strictly-unique-vue-upload-multiple-image {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}

.example-full .btn-group .dropdown-menu {
  display: block;
  visibility: hidden;
  transition: all .2s
}
.example-full .btn-group:hover > .dropdown-menu {
  visibility: visible;
}
.example-full label.dropdown-item {
  margin-bottom: 0;
}
.example-full .btn-group .dropdown-toggle {
  margin-right: .6rem
}
.example-full .filename {
  margin-bottom: .3rem
}
.example-full .btn-is-option {
  margin-top: 0.25rem;
}
.example-full .example-foorer {
  padding: .5rem 0;
  border-top: 1px solid #e9ecef;
  border-bottom: 1px solid #e9ecef;
}
.example-full .edit-image img {
  max-width: 100%;
}
.example-full .edit-image-tool {
  margin-top: .6rem;
}
.example-full .edit-image-tool .btn-group{
  margin-right: .6rem;
}
.example-full .footer-status {
  padding-top: .4rem;
}
.example-full .drop-active {
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  position: fixed;
  z-index: 9999;
  opacity: .6;
  text-align: center;
  background: #000;
}
.example-full .drop-active h3 {
  margin: -.5em 0 0;
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  font-size: 40px;
  color: #fff;
  padding: 0;
}

</style>
