<template>
  <div class="container" style="margin-top: 100px;">
    <v-card>
      <v-card-title primary-title :style="{'background-color': color}">
       <h2 class="font-weight-light" :style="{color: colorF, fontSize: '28px'}">
         {{title}}</h2>
      </v-card-title>
    <v-layout wrap>
      <!-- NADPIS -->
      <v-flex xs12>      
          <v-text-field class="px-3" v-model="title" :counter="250" label="Nadpis" required></v-text-field>
      </v-flex>

      <!-- CATEGORIA -->
      <v-flex xs12 md4>
        <v-combobox v-model="category" :items="categories" label="Kategória" class="px-3"></v-combobox>
      </v-flex>

      <!-- DATUM -->
      <v-flex xs7 md4>
        <v-menu
          v-model="menu2"
          :close-on-content-click="false"
          :nudge-right="40"
          lazy
          transition="scale-transition"
          offset-y
          full-width
          min-width="290px"
        >
          <v-text-field
            slot="activator"
            v-model="date"
            label="Dátum príspevku"
            prepend-icon="event"
            readonly
            class="px-3"
          ></v-text-field>
          <v-date-picker
            v-model="date"
            @input="menu2 = false"
            :first-day-of-week="1"
            locale="sk-sk"
          ></v-date-picker>
        </v-menu>
      </v-flex>

      <!-- COLOR -->
      <v-flex xs5 md4 class="text-xs-center">
        <v-dialog v-model="dialogColor" persistent max-width="500">
          <v-btn slot="activator" :color="color" :style="{color: colorF}">Color</v-btn>
          <v-card class="pa-3 text-xs-center">
            <v-layout wrap>
              <v-flex xs12 md6>
                <color-picker :width="200" :height="200" v-model="color" style="margin:auto;"></color-picker>
              </v-flex>
              <v-flex xs12 md6>
                <color-picker :width="200" :height="200" v-model="colorF" style="margin:auto;"></color-picker>
              </v-flex>
            </v-layout>
            
            
            <v-btn
              class="text-xs-center"
              color="green darken-1"
              flat
              @click="dialogColor = false"
            >OK</v-btn>
          </v-card>
        </v-dialog>
      </v-flex>
    </v-layout>
    </v-card>
    <v-layout wrap>
      <v-flex xs6 class="text-xs-center">
        <v-btn color="info" v-on:click="addFiles()">PRIDAŤ</v-btn>
        <label>
          <input type="file" id="photos" ref="photos" multiple v-on:change="handleFileUploads()">
        </label>
      </v-flex>
      <v-flex xs6 class="text-xs-center">
        <v-btn color="success" v-on:click="submitFiles()" :disabled="disableUpload"
        :loading="uploadProgress>0">Upload</v-btn>
      </v-flex>
    </v-layout>

    <!--     <div class="large-12 medium-12 small-12 cell">
      
     
      <img id="img1" src style="width: 100px;">
    </div>-->
    <div class="large-12 medium-12 small-12 cell">
     
        
      
      <v-list>
         <draggable :list="files"
          class="list-group"
          @start="dragging = true"
          @end="dragging = false" @change="dragChange">
          <!-- <transition-group type="transition" :name="'flip-list'"> -->
          <transition-group type="transition" :name="!dragging ? 'flip-list' : null">
        <v-list-tile v-for="(file, key) in files" :key="file.name">
          
          <v-list-tile-avatar tile>
            <img :id="'img-'+key" src>
          </v-list-tile-avatar>
          <v-list-tile-content>
            <v-list-tile-title><small class="px-1">{{key+1}}.</small>{{ file.name }}</v-list-tile-title>
          </v-list-tile-content>
          <v-list-tile-action>
            <v-btn icon ripple v-on:click="removeFile( key )">
              <v-icon color="red lighten-1">delete</v-icon>
            </v-btn>
          </v-list-tile-action>
        </v-list-tile>
          </transition-group>
        </draggable> 
      </v-list>
     
    </div>
    <v-progress-linear v-model="uploadProgress"></v-progress-linear>
  </div>
</template>

<script>
import draggable from 'vuedraggable'

var reader = new FileReader();
reader.onload = function(e) {
  document.getElementById("img1").setAttribute("src", e.target.result);
};

function readURL(imgFile, x) {
  if (imgFile && imgFile) {
    var reader = new FileReader();

    reader.onload = function(e) {
      document.getElementById("img-" + x).setAttribute("src", e.target.result);
      //$('#blah').attr('src', e.target.result);
    };

    reader.readAsDataURL(imgFile);
  }
}

//import ColorPicker from 'color-picker.vue';
import ColorPicker from "vue-color-picker-wheel";

export default {
  components: {
    ColorPicker,
    draggable
  },
  data() {
    return {
      title: "",
      file: "",
      files: [],
      uploadProgress: 0,
      picker: new Date().toISOString().substr(0, 10),
      date: new Date().toISOString().substr(0, 10),
      menu: false,
      modal: false,
      menu2: false,
      disableUpload: true,
      /* category: { text: "Candy bary", value: 1 }, */
     /*  categories: [
        { text: "Candy bary", value: 1 },
        { text: "Kytice", value: 2 },
        { text: "Sladkosti", value: 3 },
        { text: "Dekorácie", value: 4 }
      ], */
      color: "#ff6600",
      colorF: "#000",
      dialogColor: false,
      dragging: false
    };
  },
  computed: {
    categories() {
      return this.$store.state.categories
    },
        dragOptions() {
      return {
        animation: 200,
        group: "description",
        disabled: false,
        ghostClass: "ghost"
      };
    }

  },
  created() {
    this.category = this.$store.state.categories[0]
  },
  methods: {
    dragChange(e)
    {
      console.log("dragChange", e)
      this.files.forEach(function(item,x){readURL(item,x)})
    },
    addFiles() {
      this.$refs.photos.click();
    },
    removeFile(key) {
      this.files.splice(key, 1);
    },
    handleFileUploads() {
      //this.file = this.$refs.photos.files[0];
      //this.files = this.$refs.photos.files;

      let uploadedFiles = this.$refs.photos.files;

      /*
      Adds the uploaded file to the files array
      */
      for (var i = 0; i < uploadedFiles.length; i++) {
        console.log(i, uploadedFiles[i]);
        this.files.push(uploadedFiles[i]);
        //readURL(uploadedFiles[i], i);
      }
      this.files.forEach(function(item,x){readURL(item,x)})
      this.disableUpload = false;
      //reader.readAsDataURL(uploadedFiles[0]);
    },
    submitFiles() {
      let formData = new FormData();

      /*
        Iteate over any file sent over appending the files
        to the form data.
      */
      for (var i = 0; i < this.files.length; i++) {
        let file = this.files[i];

        formData.append("photos[" + i + "]", file);
      }
      formData.append("title", this.title);
      formData.append("cat_id", this.category.value);
      formData.append("color", this.color);
      formData.append("colorF", this.colorF);
      formData.append("date",this.date)
      //formData.append("photos", this.file);
      var vm = this;
      this.axios
        .post("up.php", formData, {
          headers: {
            "Content-Type": "multipart/form-data"
          },
          onUploadProgress: progressEvent => {
            console.log(progressEvent.loaded / progressEvent.total);
            this.uploadProgress =
              (progressEvent.loaded / progressEvent.total) * 100;
          }
        })
        .then(function() {
          vm.uploadProgress = 0;
          vm.files = [];
          vm.disableUpload = true;
          console.log("SUCCESS!!");
        })
        .catch(error => {
          console.log("ERROR", error);
        });
    }
  }
};
</script>

<style>
input[type="file"] {
  position: absolute;
  top: -500px;
}

div.file-listing {
  width: 200px;
}

span.remove-file {
  color: red;
  cursor: pointer;
  float: right;
}

.flip-list-move {
  transition: transform 0.5s;
}
.no-move {
  transition: transform 0s;
}
.ghost {
  opacity: 0.5;
  background: #c8ebfb;
}

</style>
