<template>
  <div>
    <v-btn @click="getData()">GET DATA</v-btn>
     <v-progress-linear v-if="loading" :indeterminate="true" :active="loading"></v-progress-linear>
    <v-timeline v-if="!loading"  dense style="padding-top: 100px;" class="lime lighten-4">
      <v-timeline-item
        class="mx-4"
        v-for="item in photos"
        :key="item.topicID"
        :color="item.color"
        small
        fill-dot
        right
      >
     <!--  <span
        
        :class="`headline font-weight-bold ${item.color}--text`"
        :style="`color: ${item.color}`"
        v-text="item.date"
      ></span>{{ $d(new Date(), 'short') }} -->
      <div class="date-label">
        <div>{{item.date.substring(8,10)}}.
        {{mmm[Number(item.date.substring(5,7))]}}</div>
        <div style="font-size: 130%;">{{item.date.substring(0,4)}}</div>
      </div>
        <!--    <v-card  :color="item.color2" 
        :style="{color: item.colorF, 'min-height':'350px'}"   
        >-->
        <v-card
          :color="item.color2" 
          :style="{color: item.colorF, 'min-height':'350px', 'background-image': item.gradient}"
        >
          <v-card-title :style="{'background-color': item.color1}">
            <v-container class="py-0">
              <h2 class="font-weight-light" :style="{color: item.colorF, fontSize: '28px'}">
              {{item.title}} 
             <!--  <span
                class="title font-weight-light"
                style="position: absolute; right: 10px; top: 28px;"
              >{{item.date}}</span> -->
            </h2>
            </v-container>

          </v-card-title>
        
     
          <v-container v-if="!item.showAll">
           
              <swiper :options="swiperOption" style="height: 290px;" v-if="!item.showAll">
              <div class="swiper-button-prev swiper-button-black" slot="button-prev"></div>
              <div class="swiper-button-next swiper-button-white" slot="button-next"></div>
              <div class="swiper-pagination" slot="pagination"></div>

              <swiper-slide v-for="(img, index) in item.images" :key="index" style="width: auto;">
                <img
                  :src="'../uploads/'+id+'/'+item.topicID+'/tiny_'+img.img"
                  style="max-height: 250px;"
                >
              </swiper-slide>
            </swiper>
            
            
          </v-container>
       

          <v-container class="pa-3">
            <!--  <infinite-slide-bar>
              <div class="columns">
                <div class="column" v-for="img in images1" :key="img">
                  <div class="f-left pa-2">
                    <img :src="img" style="max-height: 250px;">
                  </div>
                </div>
              </div>
            </infinite-slide-bar>-->
            <!--  <carousel :loop="true" :autoplay="true" :navigationEnabled="true" :scrollPerPage="false" :perPage="3">
                <slide v-for="img in images1" :key="img" style="text-align: center;">
                <img :src="img" style="max-height: 250px;">
                </slide>
  
            </carousel>-->
            <!-- <slider v-bind="setting"></slider> -->
            <v-layout wrap>
              <v-flex xs12 md10>
                <silentbox-group>
                  <silentbox-item
                    v-for="(img, index) in item.images"
                    :key="index"
                    :src="'../uploads/'+id+'/'+item.topicID+'/'+img.img"
                  >
                    <v-avatar :tile="true" :size="48" color="grey lighten-4">
                      <img
                        class="pa-1"
                        :src="'../uploads/'+id+'/'+item.topicID+'/tiny_'+img.img"
                        style="max-height: 250px;"
                      >
                    </v-avatar>
                  </silentbox-item>
                </silentbox-group>
              </v-flex>
              <v-flex xs12 md2>
                <v-btn @click="item.showAll = !item.showAll">
                  <span v-if="item.showAll">Náhľad</span>
                  <span v-if="!item.showAll">Ukázať všetko</span>
                </v-btn>
              </v-flex>
            </v-layout>
          </v-container>

         
            
          <v-container v-if="item.showAll"><!-- v-if="item.showAll" -->
           
            <silentbox-group>
              <v-layout wrap>
                <v-flex xs6 md3 v-for="(img, index) in item.images" :key="index" d-flex>
                  <silentbox-item :src="'../uploads/'+id+'/'+item.topicID+'/'+img.img">
                    <v-img
                      :src="'../uploads/'+id+'/'+item.topicID+'/tiny_'+img.img"
                      max-height="200"
                      class="ma-2"
                    >
                      <v-layout slot="placeholder" fill-height align-center justify-center ma-0>
                        <v-progress-circular indeterminate color="grey lighten-5"></v-progress-circular>
                      </v-layout>
                    </v-img>
                  </silentbox-item>
                </v-flex>
              </v-layout>
            </silentbox-group>
            
            <!--  <lightbox 
                  :images="item.imagesLight" 
                  title="Feed Gallery"
              ></lightbox> -->
          </v-container>
         
        </v-card>
      </v-timeline-item>
    </v-timeline>
  </div>
</template>

<script>
//import slider from "vue-image-scroll";
//import InfiniteSlideBar from "vue-infinite-slide-bar";
//import { Carousel, Slide } from 'vue-carousel';
import "swiper/dist/css/swiper.css";
import { swiper, swiperSlide } from "vue-awesome-swiper";
var Color = require("color");

//var mmm = ["Jan", "Feb", "Mar","Apr","Máj","Jún", "Júl","Aug", "Sep", "Okt", "Nov", "Dec"]


export default {
  components: {
    /* slider,
    InfiniteSlideBar, */
    /*  Carousel,
    Slide */
    swiper,
    swiperSlide
  },
  props: ["_title", "_items"],
  computed: {
    title() {
      //return this.$store.state.title;
      return this.$store.state.categories[this.id].text
    },
    api() {
      return this.$store.state.api
    },
    id() {
      //return this.$store.state.id;
      return this.$route.params.id;
    },
   
  },
  created() {
    console.log("component GALLERY created")
    this.getData();
    this.$store.commit("setTitle",{title: this.$store.state.categories[this.id-1].text})
  },
  mounted()
  {
    console.log("component GALLERY mounted")
  },
  updated(){
    console.log("component GALLERY updated")
    console.log("this.$store.state.loadData",this.$store.state.loadData)
    if (this.$store.state.loadData)
    {
      this.getData();
    }
  },
  
  methods: {
    swapPanel(item){
      console.log("SWAP panel", item, item.panels)
     item.panels[0] = !item.panels[0]
     item.panels[1] = !item.panels[1]
    },
    testLog() {
      console.log("test Log");
    },
    toggleShowAll(ndx) {
      console.log("showAll ", this.photos[ndx].showAll);
      this.photos[ndx].showAll = !this.photos[ndx].showAll;
      console.log("showAll", this.photos[ndx].showAll);
    },
    hideLitebox() {
      this.showLitebox = false;
    },
    getData() {
      var vm = this;
      this.loading = true
      this.axios
        .get(this.api + "sweetart/topic.php?id=" + vm.id)
        .then(response => {
          // handle success
          console.log("SUCCESS", response);
          this.loading = false
          this.$store.commit("setLoad",false)
          let dd = response.data;
          let topicID = 0;
          let newData = [];
          let x = -1;
          for (var i = 0; i < dd.length; i++) {
            let item = dd[i];
            if (item.topic_id == topicID) {
              newData[x].images.push({
                img: item.file_name,
                src: item.file_name
              });

              newData[x].imagesLight.push({
               src: '../uploads/'+vm.id+'/'+item.topic_id+'/'+item.file_name }
              );

             
            } else {
              //item.color = "rgb(125, 200, 244)"
              let color1 = Color(item.color)
                .hsl()
                .string();
              let color2 = Color(item.color)
                .lighten(0.5)
                .string();
              newData.push({
                topicID: item.topic_id,
                title: item.title,
                subtitle: item.subtitle,
                btnText: "Ukázať všetko",
                date: item.date,
                color: item.color,
                colorF: item.color_f,
                color1: color1,
                color2: color2,
                panels: [true, false],
                gradient:
                  "linear-gradient(to top left," + color1 + "," + color2 + ")",
                showAll: false,
                images: [{ img: item.file_name, src: item.file_name }],
                imagesLight: [{ src: '../uploads/'+vm.id+'/'+item.topic_id+'/'+item.file_name }]
              });
              topicID = item.topic_id;
              x++;
            }
          }
          this.photos = newData;
          // console.log("newData",newData)
          //console.log("SUCCESS",response);
        })
        .catch(function(error) {
          // handle error
          console.log("ERROR", error);
        })
        .then(function() {
          // always executed
        });
    }
  },
  data() {
    return {
      loading: true,
      panel: [true, true, true],
      mmm: ["Jan", "Feb", "Mar","Apr","Máj","Jún", "Júl","Aug", "Sep", "Okt", "Nov", "Dec"],
      showLitebox: false,
      photos: [
        {
          topicID: 0,
          title: "",
          subtitle: "",
          btnText: "",
          date: null,
          color: "",
          colorF: "",
          color1: "",
          color2: "",
          panels: [true, false],
          gradient: "",
          showAll: false,
          images: [{ img: "", src: "" }],
          imagesLight: [{ src: "" }]
        }
      ],
      swiperOption: {
        spaceBetween: 30,
        slidesPerView: "auto",
        centeredSlides: false,
        /* freeMode: true, */
        loop: false,

        autoplay: {
          delay: 2500,
          disableOnInteraction: false
        },
        navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev"
        },
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        }
      },
      setting: {
        autoRoll: true,
        imgStyle: {
          borderRadius: "5px"
        },
        image: [
          "main/FB_IMG_1549105914089.jpg",
          "main/FB_IMG_1549112091846.jpg",
          "main/FB_IMG_1549111435078.jpg",
          "main/FB_IMG_1549111914270.jpg",
          "images/kytice/FB_IMG_1549109155932.jpg"
        ],
        styleObject: {
          width: "450",
          height: "250"
        },
        interval: 2000
      },

      items: [
        { id: 0, color: "red", title: "Kytica KINDER", date: "22.4.2018" },
        { id: 1, color: "blue", title: "Kytica filaova", date: "24.6.2018" },
        { id: 2, color: "pink", title: "Modra kytica", date: "15.2.2018" }
      ],
      images1: [
        "main/FB_IMG_1549105914089.jpg",
        "main/FB_IMG_1549112091846.jpg",
        "main/FB_IMG_1549111435078.jpg",
        "main/FB_IMG_1549111914270.jpg",
        "images/kytice/FB_IMG_1549109155932.jpg",
        "images/kytice/FB_IMG_1549110519363.jpg",
        "images/kytice/FB_IMG_1549110586176.jpg",
        "images/kytice/IMG_20190204_164714.jpg",
        "images/sladkosti/FB_IMG_1549105661214.jpg",
        "images/sladkosti/IMG_20190203_140318.jpg",
        "images/sladkosti/FB_IMG_1549104710376.jpg",
        "images/sladkosti/FB_IMG_1549104426057.jpg"
      ]
    };
  }
};
</script>

<style>
.btn-show-all {
  position: absolute;
  right: 10px;
  top: 385px;
}

#silentbox-overlay__close-button {
  right: 20px !important;
}

.swiper-pagination-bullet-active {
  background-color: black;
}

.f-left {
  float: left !important;
}

.columns {
  display: flex;
}

.column {
  display: block;
  -ms-flex-preferred-size: 0;
  flex-basis: 0;
  -ms-flex-positive: 1;
  flex-grow: 1;
  -ms-flex-negative: 1;
  flex-shrink: 1;
  padding: 0.75rem;
}

.date-label
{
  position: absolute;
  top: 180px;
  left: -60px;
  text-align: center;
}
</style>
