import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    id: null,
    title: "",
    loadData: false,
    api: "http://chalupkavraji.sk/",
    categories: [
      { text: "Candy bary", value: 1 },
      { text: "Kytice", value: 2 },
      { text: "Sladkosti", value: 3 },
      { text: "Dekorácie", value: 4 }
    ],
  },
  mutations: {
    setTitle(state, payload)
    {
      state.title = payload.title;
    },
    setID(state, payload)
    {
      state.id = payload.id
    },
    setLoad(state, payload)
    {
      state.loadData = payload
    }
  },
  actions: {

  }
});
